div .oblozhka_back {
  background-color: #334950;
}

.oblozhka {
  position: absolute;
  height: 170px;
  width: 143px;
  background-color: #5f8692;
  border-radius: 8px;
}

div .book_front {
  border-color: #334950;
}

.book {
  height: 170px;
  width: 143px;
  background-color: #6a94a1;
  border: 4px solid #5f8692;
  border-radius: 8px;
  position: absolute;
  top: -25px;
  left: 0;
  z-index: 1000;
  cursor: -webkit-grab;
}

div.book:active {
  cursor: -webkit-grabbing;
}

div.book p {
  font-size: 22px;
  padding: 7px;
  font-family: Roboto;
  font-style: Regular;
  text-align: center;
  font-size: 18px;
  word-break: break-all;
}

div.book:hover > span {
  opacity: 1;
}

div.book span {
  position: absolute;
  top: -38px;
  width: 28px;
  height: 28px;
  right: -15px;
  opacity: 0;
  transition: opacity 0.25s linear;
}

div.book span button,
div.book:hover > span button {
  width: 100%;
  height: 100%;
  font-size: 15px;
  padding: 0;
  margin: 5px;
}

.pages {
  position: absolute;
  bottom: 16px;
  width: 135px;
  height: 35px;
  right: 0;
  border-radius: 9px;
  background-color: #6caabe;
  z-index: 0;
  -webkit-box-shadow: 0 6px 0 0 #b4d2c4;
  box-shadow: 0 6px 0 0 #bcdfea;
}

.bookmark {
  position: absolute;
  left: 42px;
  top: 26px;
}

div .whole_book {
  position: absolute;
  width: 143px;
  height: 190px;
}
