.navbar {
  background-color: #b7b6b6;
  height: 60px;
}

a {
  background-color: #b7b6b6;
}

.navbar-light .navbar-nav .nav-link {
  color: white;
  padding-top: 17px;
  padding-left: 10px;
}

.navbar-light .navbar-nav .nav-link:hover,
.navbar-light .navbar-nav .nav-link:focus {
  background-color: #ffc086;
  color: white;
}

div.navbar-nav {
  height: 60px;
}

.circle {
  display: block;
  width: 40px;
  height: 0;
  line-height: 0;
  padding: 20px 0;
  border-radius: 40px;
  background: #ffc086;
  color: white;
  font-size: 20px;
  text-align: center;
}

.name {
  color: white;
  margin: 10px;
  font-size: 20px;
  line-height: 23px;
  min-width: 200px;
}

div .input-group-text {
  background-color: white;
  border-radius: 20px;
  border: 3px solid #ffc086;
  border-right: none;
}

div .form-control {
  border: 3px solid #ffc086;
  border-left: none;
  border-radius: 20px;
  height: calc(1.5em + 0.75rem + 3px);
}

div .form-control:focus {
  border-color: #ffc086;
  box-shadow: none;
}

form .form-inline {
  min-width: 300px;
}
