.link {
    position: absolute;
    height:10px;
    border-top: 3px solid black;
}

.link > span {
    display: none;
    position: absolute;
    left: calc(50% - 15px);
    top: -15px;
    transition: opacity 0.25s linear;
}

.link button,
.link button:hover {
    width:30px;
    height: 20px;
    border-radius: 2px;
    font-size: 12px;
    padding: 0;
  }
