@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

.dropZone1 {
  display: block;
  position: fixed;
  border: 8px dashed gray;
  margin-left: 65px;
  width: 100%;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(199, 199, 199, 0.5);
  z-index: 3;
  cursor: pointer;
}

.dropZone2 {
  display: none;
  position: fixed;
  border: 8px dashed black;
  width: 100%;
  top: 60px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(199, 199, 199, 0.5);
  z-index: 3;
  cursor: pointer;
}

.dropText {
  position: absolute;
  top: 50%;
  left: 50%;
  color: gray;
  font-size: 50px;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}
