div.note {
  height: 150px;
  width: 150px;
  background-color: #ffddd3;
  margin: 2px 0;
  position: absolute;
  cursor: -webkit-grab;
  -webkit-box-shadow: 11px 11px 0 0 rgba(235, 255, 191);
  box-shadow: 11px 11px 0 0 rgba(235, 255, 191);
}

div.note:active {
  cursor: -webkit-grabbing;
}

div.note p {
  font-size: 14px;
  padding: 5px;
  font-family: Roboto;
  font-style: Regular;
  word-break: break-all;
}

div.note:hover > span {
  opacity: 1;
}

div.note > span {
  position: absolute;
  bottom: -40px;
  left: 20px;
  opacity: 0;
  transition: opacity 0.25s linear;
}

div.note button {
  margin-right: 12px;
  height: 20px;
  border-radius: 2px;
  font-size: 12px;
  padding: 0;
}

.btn-primary,
.btn-primary:hover {
  width: 36px;
  background-color: #7bb585;
  border-color: #7bb585;
}

div .success,
div .success:hover {
  width: 65px;
  background-color: #348541;
  border-color: #348541;
  color: white;
}

.btn-danger,
.btn-danger:hover {
  width: 60px;
  background-color: #ff4b23;
  border-color: #ff4b23;
}

div.note > textarea {
  height: 100%;
  background: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  padding: 1px;
  margin: 2px;
}

div.prishepka {
  position: absolute;
  background-color: #62727d;
  width: 52px;
  height: 13px;
  right: 44px;
  top: -7px;
  border-radius: 7px;
}

div.polosochka {
  position: absolute;
  width: 100%;
  height: 3px;
  top: 10px;
  background-color: #293945;
  border-radius: 21px;
}

img.hook {
  position: absolute;
  top: -10px;
  right: 17px;
  width: 18px;
  height: 22px;
}

