div.quote {
  height: 130px;
  width: 220px;
  background-color: #d9e8ff;
  margin: 2px 0;
  position: absolute;
  cursor: -webkit-grab;
  -webkit-box-shadow: 11px 11px 0 0 rgb(250, 210, 132);
  box-shadow: 11px 11px 0 0 rgb(250, 210, 132);
}

div.quote:active {
  cursor: -webkit-grabbing;
}

div.quote p {
  font-size: 14px;
  padding: 5px;
  font-family: Roboto;
  font-style: Regular;
  word-break: break-all;
}

div.quote:hover > span {
  opacity: 1;
}

div.quote > span {
  position: absolute;
  bottom: -40px;
  left: 20px;
  opacity: 0;
  transition: opacity 0.25s linear;
}

div.quote button {
  margin-right: 12px;
  height: 20px;
  border-radius: 2px;
  font-size: 12px;
  padding: 0;
}

div.quote > textarea {
  height: 95%;
  background: rgba(255, 255, 255, 0.5);
  font-size: 14px;
  padding: 1px;
  margin: 2px;
}
