div.note {
  height: 150px;
  width: 150px;
  background-color: #ffddd3;
  margin: 2px 0;
  position: absolute;
  cursor: -webkit-grab;
  box-shadow: 11px 11px 0 0 rgba(235, 255, 191);
}

div.note:active {
  cursor: -webkit-grabbing;
}

div.note p {
  font-size: 14px;
  padding: 5px;
  font-family: Roboto;
  font-style: Regular;
  word-break: break-all;
}

div.note:hover > span {
  opacity: 1;
}

div.note > span {
  position: absolute;
  bottom: -40px;
  left: 20px;
  opacity: 0;
  -webkit-transition: opacity 0.25s linear;
  transition: opacity 0.25s linear;
}

div.note button {
  margin-right: 12px;
  height: 20px;
  border-radius: 2px;
  font-size: 12px;
  padding: 0;
}

.btn-primary,
