.desktop {
  margin: 90px 0 0 260px;
  display: flex;
  flex-wrap: wrap;
}

.addNewDesk,
.NewDesk {
  position: relative;
  display: inline-table;
  margin: 30px 0 0 30px;
  width: 370px;
  height: 220px;
  background: rgba(229, 229, 229, 0.19);
  border: 1px solid #aab1b5;
  overflow: hidden;
  box-sizing: border-box;
}

.NewDesk:hover {
  box-shadow: 0px 0px 15px 3px #adb5bd;
}

.desktopHead {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 42px;
  background-color: #62727d;
  color: white;
  text-align: center;
  font-size: 15px;
  line-height: 42px;
  vertical-align: middle;
}

.desktopName {
  width: 325px;
  margin: auto;
}

.addButton {
  display: block;
  background-color: #ffc086;
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: auto;
}

.addButton:focus {
  outline: none;
}

.addButton:hover {
  background-color: #ffa551;
}

.btn-save {
  border: none;
  background-color: #4b606e;
  width: 30px;
  height: 40px;
  margin-left: 5px;
}

.btn-save:focus {
  outline: none;
}

.btn-save img {
  width: 100%;
}

.newDesk {
  position: absolute;
  top: 42px;
  display: flex;
  align-items: center;
  width: 100%;
  height: 178px;
}

.preview-wrapper {
  position: relative;
  height: calc(100% - 42px);
  width: 100%;
  top: 42px;
  z-index: 500;
}

.deskContent {
  position: absolute;
  top: 42px;
  display: flex;
  align-items: center;
  width: 100%;
  height: 178px;
}

.dots:after {
  content: '\2807';
  font-size: 30px;
  color: #c4c4c4;
}

div .btn-success:hover .dots:after {
  color: #3b4148;
}

div .dropdown {
  position: absolute;
  top: 0;
  left: 10px;
  height: 42px;
}

div .btn-success,
div .btn-success:hover {
  background: none;
  color: #c4c4c4;
  border: none;
  padding: 0;
  margin: 0;
  line-height: 42px;
}

div .dropdown-toggle::after {
  content: none;
}

div .btn-success:not(:disabled):not(.disabled):active,
div .btn-success:not(:disabled):not(.disabled):active:focus,
div .show > .btn-success.dropdown-toggle:focus,
div .btn-success:focus,
div .show > .btn-success.dropdown-toggle {
  background: none;
  border: none;
  box-shadow: none;
}

div .dropdown-menu {
  padding: 0;
  margin: 0;
  background-color: #293945;
  color: white;
  font-size: 12px;
  line-height: 20px;
  min-width: 135px;
  width: 135px;
}

div .dropdown-item {
  color: white;
}

div .dropdown-item:active {
  background-color: white;
  color: #16181b;
}

div .desktopHead input {
  background: none;
  height: 100%;
  border: none;
  border-bottom: 2px solid white;
  color: white;
  outline: none;
}
